import React from "react";
import ReactDOM from "react-dom";

import Backdrop from "./Backdrop";

import './Modal.css';

function Modal(props){
    const content=<div  className={`cover ${props.padding===true?"padding":null} ${props.zindex} ${props.className}`} >
     {/* <Backdrop className={`${props.className==='z-index'?'backdrop-z-index':null} ${props.zindex==="full"?"fullz":null}`} onclick={props.onclick}></Backdrop> */}
         {props.children}
    </div>
   
   return ReactDOM.createPortal(content,document.getElementById('modal'))
}

export default Modal;