import Modal from "./Modal";

import './Lobby.css';
import { profileImageSources } from "./Play";

import questionMarkSvg from './svg-src/help-circle-sharp.svg';
import { useEffect, useRef, useState } from "react";
import  { DotLoader} from "./Loader";
import Coin from "./Coin";

import Timeout from "./timeout";

import {ReactComponent as TossSvg} from './svg-src/toss.svg';
import {ReactComponent as HeadsSvg} from './svg-src/heads.svg';
import {ReactComponent as TailsSvg} from './svg-src/tails.svg';
import {ReactComponent as BattingSvg} from './svg-src/batting.svg';
import {ReactComponent as BowlingSvg} from './svg-src/bowling.svg';
import {ReactComponent as BackSvg} from './svg-src/back.svg';
import {ReactComponent as CopySvg} from './svg-src/copy.svg';
import { bot, botCall, botChoose, botToss } from "./Bot";
import { usePlayerData, useUpdatePlayerData } from "../ContextProvider";
import { play } from "./Bat";

function Lobby(props){

    const loadingImgRef=useRef();
     const playerData=usePlayerData();
       const updatePlayerData=useUpdatePlayerData();
    let roomData=props.roomData;
    
    const [lobbyData,setLobbyData]=useState({status:'',displayOtherElement:false,showRoomId:false,showStatus:false,showBackButton:false});

  
    useEffect(()=>{
        let status,displayOtherElement,timeOut;
        let showStatus=true,showBackButton=false,showRoomId=false;
        
        if(props.roomData.status=='match-making'){
            status='Searching for a player';
           if(props.roomData.gameType=='random'){

               if(props.roomData.bot==null){
                timeOut=setTimeout(()=>{
                    props.socket.connected&&props.socket.emit('got-opponent',roomData.roomId)                        
                   },Math.random()*8000)
               }
               else if(props.roomData.bot=='initiate'){
                   bot(props.roomData.roomId);
               }

           }
           else if(props.roomData.gameType=='friend'){
            status='Waiting for your friend';
            showRoomId=true;
           }
           displayOtherElement='vs';
            showBackButton=true;


        }
        else if(props.roomData.status=='toss'){
            if(roomData.tossEligiblePlayer==playerData.name){
                status='Waiting for you to toss';
                displayOtherElement='toss'
            }
            else{
                status='Waiting for the opponent to toss';
                displayOtherElement='vs';

                roomData.bot!=null&& botToss();

            }
           
        }
        else if(props.roomData.status=='tossed'){
            if(roomData.tossEligiblePlayer==playerData.name){
                status="Waiting for the opponent to call";
                displayOtherElement='toss-animation';

                roomData.bot!=null&&  botCall();
            }
            else{
                status='Waiting for you to call'
                displayOtherElement='toss-call';
            }
        }
        else if(props.roomData.status=='toss-called'){

            status="Waiting for the toss result";
                displayOtherElement='toss-animation';
        }
        else if(props.roomData.status=='toss-result'){
            if(roomData.tossWon==playerData.name){
                status="You won the toss. Waiting for you to choose"
                displayOtherElement='toss-choose';
            }
            else{
                status='Opponent won the toss. Choosing'
                displayOtherElement='vs';
                roomData.bot!=null&&botChoose();
            }
        }
        else if(props.roomData.status=='match'){
            updatePlayerData({screen:'match'})
        }
        else if(props.roomData.status=='game-aborted'){
            showBackButton=true;
            status='Opponent got disconnected'
        }
        else{
    
            status=props.roomData.status;
            displayOtherElement='vs';
        }
        setLobbyData(()=>{
            return {status,showStatus,displayOtherElement,showRoomId,showBackButton}
        })

        return ()=>{
            window.clearTimeout(timeOut);
        }
       
    },[props.roomData.status,props.roomData.bot,props.roomData.gameType])

    function chageProfileImageOnMatchMaking(){
        if(props.roomData.status=='Opponent found'){
            let playersNameArray= Object.keys(props.roomData.players);
            playersNameArray.splice(playersNameArray.indexOf(playerData.name),1);
            playerData.opponentName=playersNameArray[0]

            loadingImgRef.current.style.backgroundImage='url('+profileImageSources['male']+')';
            loadingImgRef.current.style.animationIterationCount=1;
            props.socket.connected&& props.socket.emit('acknowledgement','toss');
          
        }

    }

    async function copyToClipboard() {
        navigator.clipboard.writeText(roomData.gameRoomId)
        
    }

    function cancelMatchMaking(){
        if(props.roomData.status=='game-aborted'){
            updatePlayerData({screen:'home'});
        }
        else if(props.roomData.status=='match-making'){
            props.socket.connected&&props.socket.emit('cancel-matchmaking','Limitless Block',(response) => {
                if(response){
                    updatePlayerData({screen:'home'});
                }
            }) ;
        }
        play('back',0.9);

    }

    function initiateTossResultValidation(){
        if(props.roomData.tossEligiblePlayer!=playerData.name||roomData.bot!=null){
            setTimeout(()=>{   props.socket.connected&&props.socket.emit('validate-toss-result');   },2000);
        }
       
    }

    function initiateTossFunc(){
        play('toss');
        props.socket.connected&&props.socket.emit('toss',0.8);
    }
    function containerClickFunc(event,forWhichEvent){

        if(event.target.nodeName==='SPAN'||event.target.nodeName==='BUTTON'){
            props.socket.connected&&props.socket.emit(forWhichEvent,event.target.innerText);
            play('click',0.42);
        }
    }

    return (
        
        <Modal className="z-index" zindex={"full"}  >
           
            <div className="lobby-container border-radius-8px " >
            <div  className="lobby-profile-container d-flex flex-direction-column flex-gap-10 border-radius-8px padding-10 background-color-beige">
                <img className="lobby-profile-img border-radius-8px"   src={profileImageSources[playerData.profileImageName]}></img>
                <p className="border-radius-4px smaller-p">{playerData.name}</p>
            </div>
            <div className="lobby-timer-container remove-margin-container-auto">
            {roomData.timer!=='hide' &&<Timeout time={props.roomData.timer}></Timeout>} 
            {lobbyData.showRoomId &&<span name="game-room-id" className="smaller-span d-flex justifty-content-center flex-gap-5"><span className="background-color-beige d-flex align-items-center">Room : {props.roomData.gameRoomId}</span>  <button className="reset-button background-color-beige d-flex " onClick={copyToClipboard}><CopySvg></CopySvg></button></span>} 
            {lobbyData.displayOtherElement=='vs'&&<p className="padding-5 border-radius-4px background-color-beige h6">vs</p>}
            {lobbyData.displayOtherElement=='toss'&&<button className="rounded-button" onClick={initiateTossFunc}><span className="d-flex flex-direction-column justify-content-center align-items-center"><TossSvg></TossSvg> <span className="vsmaller-span"> Toss </span></span></button>}
            {lobbyData.displayOtherElement=='toss-animation'&&<Coin initiateTossResultValidationFunc={initiateTossResultValidation} time={Number(roomData.timer)} tossResult={roomData.tossResult}></Coin>}
            
            {lobbyData.displayOtherElement=='toss-call'&&<div className="double-rounded-button-container d-flex flex-gap-10 justify-content-center" onClick={(event)=>{
            containerClickFunc(event,'toss-call')}}>
            <button className="rounded-button heads-button "><span className="d-flex flex-direction-column justify-content-center align-items-center"><HeadsSvg className="pe-none"></HeadsSvg><span className="vsmaller-span">Heads</span></span></button>
            <button className="rounded-button tails-button"><span className="d-flex flex-direction-column justify-content-center align-items-center"><TailsSvg className="pe-none"></TailsSvg><span className="vsmaller-span">Tails</span></span></button>
            </div> }
            {lobbyData.displayOtherElement=='toss-choose'&&<div className="double-rounded-button-container d-flex flex-gap-10 justify-content-center" onClick={(event)=>{
            containerClickFunc(event,'toss-choose')}}>
            <button className="rounded-button heads-button "><span className="d-flex  flex-direction-column justify-content-center align-items-center"><BattingSvg className="pe-none"></BattingSvg><span className="vsmaller-span">Bat</span></span></button>
            <button className="rounded-button tails-button"><span className="d-flex  flex-direction-column justify-content-center align-items-center"><BowlingSvg className="pe-none"></BowlingSvg><span className="vsmaller-span">Bowl</span></span></button>
            </div> }
            </div>

            <div className="position-relative">
            <div  className="lobby-profile-container d-flex flex-direction-column flex-gap-10 border-radius-8px padding-10 background-color-beige">

 
                <div ref={loadingImgRef}   onAnimationIteration={chageProfileImageOnMatchMaking}  className="lobby-profile-img border-radius-8px" style={{backgroundImage:`url(${questionMarkSvg})`}}></div>
                <p className="border-radius-4px smaller-p d-flex justify-content-center">{playerData.opponentName==''?"\u00A0":playerData.opponentName}{playerData.opponentName==''&&DotLoader()}</p>
 
                {/* {lobbyData.showScreen&&<span className="opponent-status smaller-span">{lobbyData.status}</span>} */}

                
            </div>
            </div>
            {lobbyData.showBackButton&&<button className="cancel-matchmaking remove-margin-container-auto d-flex justify-content-center align-items-center" onClick={cancelMatchMaking}><BackSvg></BackSvg></button>}
            {lobbyData.showStatus&&<span className="lobby-status smaller-p background-color-beige">{lobbyData.status}</span>}
            </div>
            </Modal>
    )
}

export default Lobby;