import React, { useEffect} from "react";
import { useState,useRef } from "react";


import Modal from './Modal';

import './Main.css';
import Timeout from "./timeout";
import { play} from "./Bat";
import { botAnimationDone, triggerRun } from "./Bot";
import { loader, profileImageSources } from "./Play";
import {  playFour } from "./Canvas";

// SVG
import {ReactComponent as BattingSvg} from './svg-src/batting.svg';
import {ReactComponent as BowlingSvg} from './svg-src/bowling.svg';
import {ReactComponent as DisableSvg} from './svg-src/disable.svg';
import { usePlayerData, useUpdatePlayerData } from "../ContextProvider";



export var timerWatcher={time:0,playerTookAction:false};
export var botRun=[];
export var botwantstoknow;
export var playerbat;
export var playerbowl,runDis={run:0};





export const runClass={1:'one',2:'two',3:'three',4:'four',5:'five',6:'six','W':'zero',0:'zero'};
function Main(props){
    const playerData=usePlayerData();
    const updatePlayerData=useUpdatePlayerData();

    
    
    {/*WTC FINAL*/}
    const [teamNames,setTeamNames]=useState({got:false,teamNames:[]});
    const [contributed,setContributed]=useState(false);
    const [disable,setDisable]=useState(false);
    var contribute=useRef('');
     {/*WTC FINAL*/}
    function animationFinishedFunc(){
      
        props.socket.connected&&props.socket.emit('animation-done');
        props.roomData.bot!=null&&botAnimationDone();
        }
    function emitRun(num){
        setDisable(()=>{
            return  true;
        })
        let checkedNum=Number(num);
        isNaN(checkedNum)===false&&  props.socket.connected&&props.socket.emit('emit-run-data',checkedNum);
        props.roomData.bot!=null&&triggerRun(props.roomData,num,playerData.name,playerData.opponentName);
       
    }
   

    async function getTeamNames(){
        const res=await fetch(`https://www.handcricket.in/tournament/team_available`,{cache: "no-cache"});
        const data=await res.json();
        const tempArray=[];
        
        data.forEach((e,ind)=>{
           if(e!=null){
            tempArray.push({name:e.team1[0],teamNumber:`-team1-${ind}`});
            tempArray.push({name:e.team2[0],teamNumber:`-team2-${ind}`});
           }

        })

        for (let i = tempArray.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [tempArray[i], tempArray[j]] = [tempArray[j], tempArray[i]];
        }
        setTeamNames((prev)=>{return({...prev,teamNames:tempArray,got:true})});

    }
  


    
    useEffect(()=>{
        loader.animationMixer.addEventListener('finished',animationFinishedFunc);
        runDis.run=props.roomData.run;



        if(teamNames.got===false && props.roomData.gameType==='random'){
            getTeamNames();
        }

        if(props.roomData.bot!=null&&props.roomData.matchResult!=null){
            botContributePoints()
        }
        if(props.roomData.inputRecieved===props.roomData.totalInputNeeded){
            playFour( props.roomData.run)
        }

        setDisable(()=>{
            return  props.roomData.players[playerData.name].disable;
        })

        return(()=>{
            loader.animationMixer.removeEventListener('finished',animationFinishedFunc);
        })
      
        
    },[props.roomData.inputRecieved,props.roomData.matchResult]);
   
    function reset(){
        
        let runScored=props.roomData.players[playerData.name].runs;

        if(props.roomData.gameType=='random'){
        if(props.roomData.players[playerData.name].wicketTaken!=0){
            localStorage.setItem('wickets',Number(localStorage.getItem('wickets'))+props.roomData.players[playerData.name].wicketTaken);
        }
        
        localStorage.setItem('match',Number(localStorage.getItem('match'))+1);
        localStorage.setItem('score',Number(localStorage.getItem('score'))+runScored);

        localStorage.setItem('avg',Number(localStorage.getItem('score'))/Number(localStorage.getItem('match')));
        if(runScored>=100){
            localStorage.setItem('hundreds',Number(localStorage.getItem('hundreds'))+1);
        }
        else if(runScored>=50){
            localStorage.setItem('fifties',Number(localStorage.getItem('fifties'))+1);
        }
        else if(runScored>=30){
            localStorage.setItem('thirties',Number(localStorage.getItem('thirties'))+1);
        }
    } 
        props.socket.disconnect();
        updatePlayerData({screen:'home'});
       
    }

 
    
 {/*WTC FINAL*/}
 function botContributePoints(){
    let botContribute=[];
    teamNames.teamNames.forEach((e,ind)=>{
        if(e!=null){
            botContribute.push(`${e.name}-${props.roomData.players[playerData.opponentName].eligiblePoints}-${props.roomData.players[playerData.opponentName].runs}-${props.roomData.players[playerData.opponentName].wicketTaken}-${playerData.opponentName}${e.teamNumber}`);             
        }

});

contributePoints(botContribute[Math.floor(Math.random()*botContribute.length)],props.roomData.players[playerData.opponentName].id);
}
async function contributePoints (contributionData,playerId){
    props.roomData.players[playerData.name].id===playerId&&setContributed(true);
        await fetch('https://www.handcricket.in/tournament/contribute',{method:"POST",body:JSON.stringify({data:contributionData.split("-"),playerId}), headers: {
                'Content-type': 'application/json; charset=UTF-8',
             }});
       
        
    }
 {/*WTC FINAL*/}

    
    return(
    <React.Fragment>
    
    <div id="container">

        <div className="top-container  remove-margin-container-auto">
  
       <div className="score d-flex  flex-direction-column align-items-center"><img src={profileImageSources[props.roomData.players[playerData.name].profileImgName]} className='img-profile'></img>
                <div className="d-flex justify-content-center padding-10">
                    <h6 className="player-name">{playerData.name} :&nbsp;</h6><span className='h6'>{props.roomData.players[playerData.name].runs} / {props.roomData.players[playerData.name].wicketLost}</span>
                
                    </div> 

            </div>

            <div className="main">
    <p className="padding-5 smaller-p">{props.roomData.gameMode}</p>
    <div>
        <p className="span">OVER: {props.roomData.over}</p>
        <p className="smaller-span">CRR: {props.roomData.CRR}</p>
        <p className="smaller-span">RRR: {props.roomData.RRR}</p>
    </div>
    <h2 className={`run ${runClass[props.roomData.run]} d-flex justify-content-center align-items-center`}>{props.roomData.run}</h2>
    {props.roomData.turn==0 &&<span className="padding-5" >{props.roomData.batting==playerData.name?'You' : props.roomData.batting} scored {props.roomData.players[props.roomData.batting].runs} in {props.roomData.currentBall} balls</span>}
    {props.roomData.turn>0 &&<span className="padding-5" >{props.roomData.batting==playerData.name?'You' : props.roomData.batting} need {((props.roomData.players[props.roomData.bowling].runs)-(props.roomData.players[props.roomData.batting].runs))+1} runs {props.roomData.totalBalls=='10000'?null:`in ${props.roomData.totalBalls-props.roomData.currentBall} balls`} </span>}


</div>
     <div className="score d-flex flex-direction-column align-items-center">
     <img src={profileImageSources[props.roomData.players[playerData.opponentName].profileImgName]} className='img-profile'></img>

    <div className="d-flex justify-content-center padding-10"><h6 className="player-name">{playerData.opponentName} :&nbsp;</h6> <span className='h6'>{props.roomData.players[playerData.opponentName].runs} / {props.roomData.players[playerData.opponentName].wicketLost}</span></div> 
                
     
            
            

            </div>
            <div className="d-flex justify-content-center flex-gap-5">
<p className={`${runClass[props.roomData.currentOverRuns[0]]}-color`}> {props.roomData.currentOverRuns[0]}</p>
<p className={`${runClass[props.roomData.currentOverRuns[1]]}-color`}>{props.roomData.currentOverRuns[1]}</p>
<p className={`${runClass[props.roomData.currentOverRuns[2]]}-color`}>{props.roomData.currentOverRuns[2]}</p>
<p className={`${runClass[props.roomData.currentOverRuns[3]]}-color`}>{props.roomData.currentOverRuns[3]}</p>
<p className={`${runClass[props.roomData.currentOverRuns[4]]}-color`}>{props.roomData.currentOverRuns[4]}</p>
<p className={`${runClass[props.roomData.currentOverRuns[5]]}-color`}>{props.roomData.currentOverRuns[5]}</p>
</div>
    </div>
        <div className="bottom-container">
   
        {props.roomData.timer!=='hide' &&<Timeout time={props.roomData.timer}></Timeout>} 
        <div className="player-status-container">
       {props.roomData.batting==playerData.name?<><BattingSvg></BattingSvg><span className="p">Batting</span></>:<><BowlingSvg></BowlingSvg><span className="p">Bowling</span></>} 
    </div>
        <div className="run-button-container" >

            <button onClick={()=>{emitRun(1)}} className="button one" disabled={disable}><span className="button-span one run">{disable?<DisableSvg></DisableSvg>:1}</span></button>
            <button onClick={()=>{emitRun(2)}} className="button two" disabled={disable}><span className="button-span two run">{disable?<DisableSvg></DisableSvg>:2}</span></button>
            <button onClick={()=>{emitRun(3)}} className="button three" disabled={disable}><span className="button-span three run">{disable?<DisableSvg></DisableSvg>:3}</span></button>
            <button onClick={()=>{emitRun(4)}} className="button four" disabled={disable}><span className="button-span four run">{disable?<DisableSvg></DisableSvg>:4}</span></button>
            <button onClick={()=>{emitRun(5)}} className="button five" disabled={disable}><span className="button-span five run">{disable?<DisableSvg></DisableSvg>:5}</span></button>
            <button onClick={()=>{emitRun(6)}} className="button six" disabled={disable}><span className="button-span six run">{disable?<DisableSvg></DisableSvg>:6}</span></button>
        </div>
        </div>

        {/*WTC FINAL*/}
        {(props.roomData.matchResult!=null&&!contributed&&props.roomData.animationDone==2&&props.roomData.gameType=='random'&& (props.roomData.players[playerData.name].eligiblePoints!=0||props.roomData.players[playerData.name].runs>0))&&<Modal padding={true} className="z-index">
            <h2 className="match-result">Country Cup</h2>
            <div>
            <label htmlFor="contribute">{`Contribute your ${props.roomData.players[playerData.name].eligiblePoints} points ${props.roomData.players[playerData.name].runs} runs to`}</label>
            <select id="contribute" ref={contribute}>
                {teamNames.teamNames.map((e,ind)=>{if(e!=null){
                    return(
                    <option key={ind} value={`${e.name}-${props.roomData.players[playerData.name].eligiblePoints}-${props.roomData.players[playerData.name].runs}-${props.roomData.players[playerData.name].wicketTaken}-${playerData.name}${e.teamNumber}`} className="contribute-option">{e.name}</option>)
                }})}

            </select>
            </div>
            <button className="button play-button" onClick={()=>{play('click',0.42);contributePoints(contribute.current.value,props.roomData.players[playerData.name].id)}}><span className='button-span toss-button-span'>Contribute</span></button>
            </Modal>}
           
          {/*WTC FINAL*/}                                                                                                                                                                                                                                                                                             

                                              

        {((props.roomData.matchResult!=null&&props.roomData.animationDone==2&&(contributed || props.roomData.gameType=='friend'||(props.roomData.players[playerData.name].eligiblePoints==0&&props.roomData.players[playerData.name].runs==0)))||props.roomData.status=='game-aborted')&&
        <Modal padding={true} className="z-index">
            <div className='d-flex flex-direction-column flex-gap-15'>
            <h2 className="match-result">Match {props.roomData.matchResult=='draw'?'Draw':props.roomData.matchResult==playerData.name?'Won':props.roomData.status=='game-aborted'?'Ended':'Lost'}</h2>
            {props.roomData.status==='game-aborted'&& <p className="h6">Opponent got disconnected</p>}
            <button className="button play-button" onClick={()=>{play('click',0.42);reset()}}><span className='button-span toss-button-span'>main Menu</span>
            </button>
            </div>
        </Modal>}
        </div>
        </React.Fragment>)
}

export default Main;