import React, { useEffect, useState,useRef} from "react";


// css
import './timeout.css';

let whichElementIsCentered,previousTime,currentTime,futureTime=null;
function Timeout(props){

    const timerRef=useRef(null);

    useEffect(()=>{
        
        if(props.time!='hold' && currentTime!=props.time){
            if(typeof props.time=='string'){
                currentTime= Number(props.time.split('-begin')[0])
            }
            else{

                currentTime=props.time;
            }
            previousTime=currentTime-1;
            futureTime=currentTime+1;
        if(currentTime<=3){
              timerRef.current.className='alert'
        }
        else if(currentTime<=6){

              timerRef.current.className='warning'
        }

        
          
        if(whichElementIsCentered==null || whichElementIsCentered==2){
            timerRef.current.children[1].className='middle-time p'
            timerRef.current.children[2].className='bottom-time p'
            timerRef.current.children[0].className='top-time p'

            timerRef.current.children[0].innerText=previousTime;
            timerRef.current.children[1].innerText=currentTime;
            timerRef.current.children[2].innerText=futureTime;

            whichElementIsCentered=1;
        }
        else if(whichElementIsCentered==1){


            timerRef.current.children[0].innerText=currentTime;


            timerRef.current.children[1].innerText=futureTime;

            timerRef.current.children[2].innerText=previousTime;
;
            timerRef.current.children[1].className='bottom-time p'
            timerRef.current.children[0].className='middle-time p'
            timerRef.current.children[2].className='top-time p'
            whichElementIsCentered=0;
        }
        else if(whichElementIsCentered==0){

 
            timerRef.current.children[0].innerText=futureTime;

            timerRef.current.children[1].innerText=previousTime;

            timerRef.current.children[2].innerText=currentTime;
    
            timerRef.current.children[2].className='middle-time p'
            timerRef.current.children[0].className='bottom-time p'
            timerRef.current.children[1].className='top-time p' 
            whichElementIsCentered=2;
        }
    
    }
    },[props])
    return(
        <>
         {/* <span >{previousTime}</span> */}
        <div className='d-flex flex-direction-column' name='timer' ref={timerRef}>
 
            <span ></span>
            <span ></span>
            <span ></span>

        </div>
        </>
    )
}

export default Timeout;