import { useRef } from "react";
import { back, play } from "./Bat";
import Modal from "./Modal";
import { usePlayerData, useUpdatePlayerData } from "../ContextProvider";
;

function FriendRoom(props){

    
   const roomId=useRef(null);
    const playerData=usePlayerData();
    const updatePlayerData=useUpdatePlayerData();

    function joinOrCreateRoom(roomType,action){
        props.socket.connected==false&&props.socket.connect();
        props.socket.emit('join-room',playerData.name,playerData.profileImageName,{roomType,action,gameRoomId:roomId.current.value},'Limitless Block');
   
        updatePlayerData({screen:'lobby'})
    }

    return (
        <Modal padding={true}>
            
            <div className='room-container'>
            <button className="back-button" onClick={()=>{play('back',0.9);updatePlayerData({screen:'home'})}}>{back}</button>
            <h2>Friend's room</h2>
            <div>
                
            <h5>Create a room</h5>
            <button className="button play-button" onClick={()=>{play('click',0.42);joinOrCreateRoom('friend','create')}} ><span className={`button-span play-button-span`}>Create</span></button>
            </div>
            <div>
            <h5>Join a room</h5>
            <input id='room-name' placeholder='Room Code' type="text" ref={roomId}></input>
            <button className="button play-button" onClick={()=>{play('click',0.42);joinOrCreateRoom('friend','join')}} ><span className={`button-span play-button-span`}>Join</span></button>
            </div>
            </div>
        </Modal>
    )
}

export default FriendRoom;