const AudioContext = window.AudioContext || window.webkitAudioContext;
var audioContext,gainNode,source;
var sound={};
export function createAudioContext(){
	if(audioContext ===undefined){
		audioContext = new AudioContext();
		gainNode = audioContext.createGain();
		source=audioContext.createBufferSource();
		fetch(clicksound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound['click']=dec;
		}).catch(e=>{});
		fetch(backsound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound['back']=dec;
		}).catch(e=>{});
		 fetch(tosssound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound['toss']=dec;
		}).catch(e=>{});

		fetch(wicketsound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound['W']=dec;
		}).catch(e=>{});

		fetch(onesound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound[1]=dec;
		}).catch(e=>{});

		fetch(twosound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound[2]=dec;
		}).catch(e=>{});

		fetch(threesound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound[3]=dec;
		}).catch(e=>{});

		fetch(foursound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound[4]=dec;
		}).catch(e=>{});
		
		fetch(fivesound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound[5]=dec;
		}).catch(e=>{});

		fetch(sixsound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound[6]=dec;
		}).catch(e=>{});
		
		fetch(clocksound).then((res)=>{return res.arrayBuffer()}).then(buffer => audioContext.decodeAudioData(buffer))
		.then(dec=>{ 
			sound['clock']=dec;
		}).catch(e=>{});




	}

}

export function play(what,volume=1){
	if(playquest){
		stopSound();
		source=audioContext.createBufferSource();
	
		
		source.buffer=sound[what]
		gainNode.gain.value=volume;
		source.connect(gainNode).connect(audioContext.destination);
	
		source.start();
	}
	else{
		return;
	}


}
export function stopSound(){
	if(source.buffer!==null){
		source.stop();
}
}
