import { useRef, useState } from "react";
import Modal from "./Modal";
import { back, feedbacksvg, play } from "./Bat";

function Survey(props){

    const [selectedOption,setSelectedOption]=useState('');
    const feedbackText=useRef('');

    let options=['2 Over game','3 Over game','Unlimited Over game (current) '];

    function optionChange(gameMode){
        setSelectedOption(()=>{
            return gameMode;
        })
    }
    async function submitSurvey(){
    
        if(selectedOption.length>0){
            const bodyObj={};
            bodyObj['selectedOption']=selectedOption;
            bodyObj['feedback']=feedbackText.current.value;
            props.exit();
           const response= await fetch('https://www.handcricket.in/leaderboard/survey',{method:"POST",body:JSON.stringify(bodyObj), headers: {
                'Content-type': 'application/json; charset=UTF-8',
             }});
             
             if(response.status==201){
                localStorage.setItem('game-mode-survey','done');
                
             };
       

        }   
        

    }

    return (
        <>
         <Modal className="z-index" zindex={"full"} padding={true} >
            <div className="d-flex flex-direction-column flex-gap-15">
            <div className="Leaderboard-main-heading remove-margin-container-auto">
            <button className="back-button" onClick={()=>{play('back',0.9);props.exit()}} >{back}</button>
            <h2 className="justify-content-center">Game Feedback {feedbacksvg}</h2>
            </div>
       

        <p className="smaller-p">We are considering replacing our current Unlimited Over Game mode with a new game mode based on user votes. Please let us know which of the following game modes you would find most interesting:</p>
        <div className="d-flex flex-direction-column flex-gap-5 remove-margin-container-auto" style={{paddingLeft:'30px'}}>

        {options.map((choice, index) => (

<label className="p radio-set"  key={index}><input type="radio" name="mode" 
 value={choice} 
 key={index}
 checked={selectedOption === choice}
 onChange={(e) => optionChange(e.target.value)}></input>{choice}</label>

      ))} 
        </div>

        <div style={{width:'80%'}}  className="d-flex flex-direction-column flex-gap-5">
        <p className="span">Other feedbacks:</p>
        <textarea maxLength={300} ref={feedbackText}></textarea>
        </div>

        <span>Thanks for supporting the game this far. We are trying to improve the game with your valuable feedbacks</span>


        <button class="button" onClick={submitSurvey}><span class="button-span play-button-span "><p class="play-icon-text">Submit</p></span></button>
        </div>
        </Modal>
        </>
    )
}

export default Survey;