import { back, play } from "./Bat";
import Modal from "./Modal";
import './Fixture.css';
import React, { useEffect, useRef, useState } from "react";
import Cap from "./Cap";

function Fixture(props) {

  const matchName={25:"Qualifier 1",26:"Eliminator",27:"Qualifier 2",28:"Final"};
  const [openMom,setOpenMom]=useState(null)
  const scrollTo=useRef();
  let currentDateAndTime=new Date(props.currentDate.getTime());
  let nextMatchDateAndTime,endsInLeague;
  if(currentDateAndTime.getDate()>=28){
    nextMatchDateAndTime= new Date(currentDateAndTime.getFullYear(), currentDateAndTime.getMonth()+1,1 );
    nextMatchDateAndTime.setHours(0,0,0);
    let hrsLeft=Math.floor(((nextMatchDateAndTime-currentDateAndTime)/(1000*60*60))).toFixed(0);
    let minsLeft=(((nextMatchDateAndTime-currentDateAndTime)/(1000*60))%60).toFixed(0);
    endsInLeague=(hrsLeft+(hrsLeft>1?'Hrs ':'Hr '))+(minsLeft+(minsLeft>1?'mins':'min')) ;
  }
  else{
    nextMatchDateAndTime=new Date(props.currentDate.getTime());
    nextMatchDateAndTime.setDate(nextMatchDateAndTime.getDate()+1);
    nextMatchDateAndTime.setHours(0,0,0);
    let hrsLeft=Math.floor(((nextMatchDateAndTime-currentDateAndTime)/(1000*60*60))).toFixed(0);
    let minsLeft=(((nextMatchDateAndTime-currentDateAndTime)/(1000*60))%60).toFixed(0);
    endsInLeague=(hrsLeft+(hrsLeft>1?'Hrs ':'Hr '))+(minsLeft+(minsLeft>1?'mins':'min')) ;
  }
  function enableMom(num){
    setOpenMom(num);
  }

  useEffect(()=>{
    setTimeout(()=>{  
      scrollTo.current.scrollIntoView({ behavior: 'smooth', block:currentDateAndTime.getDate()>=28 ?'end': 'center', inline: 'start' });
    },100)
   
  },[])

  return (
   
    <Modal zindex={"full"} className={"fixture"}>
       {openMom!=null  && <Cap click={()=>{enableMom(null)}} show={openMom}></Cap>}
      <div className="tournament-container">
        <div className={`tournament-heading-container`}>
          <div className="tournament-heading-container-1">
            <button className="back-button" onClick={
              ()=>{
                props.click();play('back',0.9)}}>
              {back}
            </button>
            <h2>Fixture</h2>
          </div>
        </div>
        <div className="fixture-one-data-wrapper">
        {props.fixtures!=undefined && Object.keys(props.fixtures).map((key)=>{
            if(key!='_id')
            return(
                <React.Fragment  key={key} >
                <div className="fixture-one-data-container" id={key} ref={props.whichDateDataToShow==key ?scrollTo:null} onClick={()=>{((currentDateAndTime.getDate()==key)||(currentDateAndTime.getDate()>=28&&key==28))&&enableMom(0+'--'+props.fixtures[key][0].team1[0]+' vs '+props.fixtures[key][0].team2[0])}}>

                  <div>
                       <span>{key} - {props.currentMonth}</span>
                       <span>{key<=24 ?"League Match":matchName[key]}</span>
                  </div>
                
      
                <div>
                  <div className="fixture-one-data">
                  <img src={require(`../images/flags/${props.fixtures[key][0].team1[0]}.png`)}></img>
                  <div >
                      <span>{props.fixtures[key][0].team1[0]}</span>
                      <p>{props.fixtures[key][0].team1[1]}  <span>({props.fixtures[key][0].team1[2]})</span> </p>
                  </div>
                    
                  </div>
                  <p>VS</p>
                  <div className="fixture-one-data">
                  <img src={require(`../images/flags/${props.fixtures[key][0].team2[0]}.png`)}></img>
                  <div>
                      <span>{props.fixtures[key][0].team2[0]}</span>
                      <p>{props.fixtures[key][0].team2[1]} <span>({props.fixtures[key][0].team2[2]})</span></p>
                  </div>
                    
                  </div>
                </div>
                
                <div>
                  <span>MoM - <span>{props.fixtures[key][0].mom == ''?'yet to be decided' : props.fixtures[key][0].mom }</span></span>
                  <span>Won - <span>{props.fixtures[key][0].won == ''?'yet to be decided' : props.fixtures[key][0].won}</span></span>
                </div>
                <span className="match-status">{currentDateAndTime.getDate()>=28&&key==28?`Ends in ${endsInLeague}`:currentDateAndTime.getDate()>key?'Ended':currentDateAndTime.getDate()==key?`Ends in ${endsInLeague}`:key - currentDateAndTime.getDate()==1?`Starts in ${endsInLeague}`:`Starts in ${key - currentDateAndTime.getDate()} days`}</span>
              </div>

   {props.fixtures[key][1]!=undefined&& <div className="fixture-one-data-container" onClick={()=>{((currentDateAndTime.getDate()==key)||(currentDateAndTime.getDate()>=28&&key==28))&&enableMom(1+'--'+props.fixtures[key][1].team1[0]+' vs '+props.fixtures[key][1].team2[0])}}>
   <div>
                       <span>{key} - {props.currentMonth}</span>
                       <span>{key<=24 ?"League Match":matchName[key]}</span>
                  </div>
                

<div>
  <div className="fixture-one-data">
  <img src={require(`../images/flags/${props.fixtures[key][1].team1[0]}.png`)}></img>
  <div >
      <span>{props.fixtures[key][1].team1[0]}</span>
      <p>{props.fixtures[key][1].team1[1]} <span> ({props.fixtures[key][1].team1[2]})</span></p>
  </div>
    
  </div>
  <p>VS</p>
  <div className="fixture-one-data">
  <img src={require(`../images/flags/${props.fixtures[key][1].team2[0]}.png`)}></img>
  <div>
      <span>{props.fixtures[key][1].team2[0]}</span>
      <p>{props.fixtures[key][1].team2[1]} <span> ({props.fixtures[key][1].team2[2]}) </span></p>
  </div>
    
  </div>
</div>

<div>
                  <span>MoM - <span>{props.fixtures[key][1].mom == ''?'yet to be decided' : props.fixtures[key][1].mom }</span></span>
                  <span>Won - <span>{props.fixtures[key][1].won == ''?'yet to be decided' : props.fixtures[key][1].won}</span></span>
</div>
<span className="match-status">{currentDateAndTime.getDate()>=28&&key==28?'Ends in month end':currentDateAndTime.getDate()>key?'Ended':currentDateAndTime.getDate()==key?`Ends in ${endsInLeague}`:key - currentDateAndTime.getDate()==1?`Starts in ${endsInLeague}`:`Starts in ${key - currentDateAndTime.getDate()} days`}</span></div>}
</React.Fragment>
            )
        })}
    </div>
      </div>
      <div className="note-cover">
        <div className="note-container remove-margin-note-container">
          <p className="note-1">Note</p>
          <p className="note-2">
            Click on the current date match fixture to view the top contributors
            of the match (MoM)
          </p>
        </div>
      </div>
    </Modal>
  );
}

export default Fixture;
