import React, { useEffect, useRef, useState } from 'react';


import './Play.scss';

import Main from './Main';
import { random,friend,back, leaderboardsvg, profilesvg, Countrycuptrophy,play, createAudioContext } from './Bat';
import femaleProfileImg from '../images/female/female1.png';
import maleProfileImg from '../images/female/male1.png';
import Leaderboard from './Leaderboard';
import Profile from './Profile';


import Countrycup from './Countrycup.js';
import Canvas, { exportDone } from './Canvas';





import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import * as T from 'three';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader';
import Survey from './Survey.js';
import Lobby from './Lobby.js';
import FriendRoom from './FriendRoom.js';
import { usePlayerData, useUpdatePlayerData } from '../ContextProvider.js';



export var socket={};

export var botTimeout;
export var playquest;

export var loader={glb:'',animationDone:false}


export var whoDisconnect="",gameName="",gameImg="male";

const profileImageNames=['male','female'];
export const profileImageSources={'male':maleProfileImg,'female':femaleProfileImg}
let loadingManager;
 function load(){
    const draco=new DRACOLoader();
    draco.setDecoderPath('./draco/');
    loadingManager=new T.LoadingManager();
    const glft=new GLTFLoader(loadingManager);
 
    glft.setDRACOLoader(draco);
    glft.load('./handcricket.glb',(glb)=>{
        loader.glb=glb;
        glb.scene.traverse((s)=>{
          if(s.name==='ball'){
            
            s.geometry.computeBoundingSphere();
            loader.boundingSphere=s.geometry.boundingSphere;
            loader.boundingSphere.center=glb.scene.children[37].children[5].position;

            
          }
          else if (s.name==="bat"){
            s.geometry.computeBoundingSphere();
            // s.geometry.boundingSphere.radius=0.4;
            loader.boundingBox=s.geometry.boundingSphere;
            loader.boundingBox.center=glb.scene.children[37].children[6].position;
          }
          else if(s.name==="stump"){
            s.geometry.computeBoundingSphere();
            loader.boundingStump=s.geometry.boundingSphere
            loader.boundingStump.center=glb.scene.children[37].children[9].position;
          }
        })
        loader.animationMixer=new T.AnimationMixer(glb.scene);
        loader.clips=glb.animations;
        exportDone();
        
        
      })
}
load();
function Play(props){

    const nameRef=useRef();

    const [loading,setloading]=useState({loading:true,progress:0});
    const [showScreen,setShowScreen]=useState(false);
    const [roomData,setRoomData]=useState(false);

    const playerData=usePlayerData();
    const updatePlayerData=useUpdatePlayerData();
       useEffect(()=>{

            createAudioContext();

            // check if the user filled the survey
            // if(localStorage.getItem('game-mode-survey')!='done'){
            //     setShowScreen('survey');
            // }

            // set Name
            updatePlayerData({name:localStorage.getItem('name'),profileImageName:localStorage.getItem('img')==null?'male':localStorage.getItem('img'),soundEffect:localStorage.getItem('soundEffect')=='true'});
            
            playquest=localStorage.getItem('soundEffect')=='true';


            // loading of assets
            loadingManager.onProgress=(url,loaded,total)=>{
                setloading((state)=>{
                    return {...state,progress:(loaded/total).toFixed(2)};
                })
            };
            loadingManager.onLoad=()=>{
                setloading((state)=>{
                    return {...state,loading:false};
                })
            };  

            // listening to room data
            props.socket.on('room-data',(roomData)=>{
              setRoomData(()=>{
                if(roomData==null){
                    updatePlayerData({screen:'home'});
                    return false
                }
                return roomData
              })
            })

        }
   
           ,[])
   
           function setShowScreenFalseFun(){
            setShowScreen(false);
           }

  
    function toggleSoundEffects(e){
        playquest=e.target.checked;
        updatePlayerData({soundEffect:e.target.checked});
        localStorage.setItem('soundEffect',e.target.checked);
        
    }

    function validateUser(){
        if(playerData.name.length<4){
            nameRef.current.focus();
            return false;
        }

        localStorage.setItem('name',playerData.name);
        localStorage.setItem('img',playerData.profileImageName);
        updatePlayerData({name:localStorage.getItem('name'),profileImageName:localStorage.getItem('img')});
        return true;

  
    }
    function profileImgChangeFunc(event){
        updatePlayerData({profileImageName:event.target.value});
  

    }

    function joinRoom(roomType){
       setRoomData(()=>{
        return false;
       })
        if(validateUser()){
            if(roomType=='random'){
                props.socket.connected==false&&props.socket.connect();
                props.socket.emit('join-room',playerData.name,playerData.profileImageName,{roomType},'Limitless Block');
                updatePlayerData({screen:'lobby',opponentName:''});
            }
           else{
            updatePlayerData({screen:'friend-room'});
           }
           
        

               
        }
    }
   
    
     if(playerData.screen==='home'){

        return(
            <React.Fragment>
                <h1>{playerData.soundEffect}</h1>
            <div className='cover cover-down'>
                
            <div className='title'><h1>Hand <br></br>Cricket.io</h1></div>
            <input ref={nameRef} id='name' placeholder='Name (4-6 characters)' type="text" minLength="4" maxLength="6" value={playerData.name} onChange={(event)=>{updatePlayerData({name:event.target.value})}}></input>
            <div className='profile-img-card-container'>
            {
                profileImageNames.map((profileImageName)=>{
                    return(
                        <label className='profile-img-card' key={profileImageName}><input type='radio' name='profile-img-radio' value={profileImageName} checked={playerData.profileImageName==profileImageName} onChange={profileImgChangeFunc}  ></input><img alt='hand cricket game male character' loading="lazy" src={profileImageSources[profileImageName]} className='profile-img'></img>
            </label>
                    )
                })

            }

            </div>

            <div className='play-area'><hr/> <span> Play </span> <hr/> </div>
            <div className={`${!loading.loading?'button-set':''}`}>
                {!loading.loading&&<button className="button play-button"  onClick={()=>{play('click',0.42);joinRoom('random')}} ><span className="button-span play-button-span ">{random}<p className='play-icon-text'>&nbsp;&nbsp;&nbsp;Random&nbsp;&nbsp;&nbsp;</p></span></button>}
            
                {!loading.loading&&<button className="button play-button" onClick={()=>{play('click',0.42);joinRoom('friend')}} ><span className="button-span play-button-span">{friend}<p className='play-icon-text'>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Friend&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p></span></button>}
                {loading.loading&&<React.Fragment><div>Cleaning the grounds<div className='dots'><span className='dot1'>.</span><span className='dot2'>.</span><span className='dot3'>.</span></div></div><span style={{fontSize:'12px'}}>{`${loading.progress*100}% completed`}</span></React.Fragment>}
                </div>
            <div className='play-area leaderboard-area'><hr/> <span>Check </span> <hr/> </div>
            <div className='button-set check'>
            <button className="button leaderboard-button" onClick={()=>{play('click',0.42);setShowScreen("leaderboard")}} ><span className={`button-span play-button-span `}>{leaderboardsvg}<p className='play-icon-text'>Leaderboard</p></span></button>
                <button className="button leaderboard-button" onClick={()=>{play('click',0.42);setShowScreen("profile")}} ><span className={`button-span play-button-span `}>{profilesvg}<p className='play-icon-text'>&nbsp;My Profile&nbsp;</p></span></button>
                <button className="button leaderboard-button"   onClick={()=>{play('click',0.42); setShowScreen("country_cup")}} ><span className={`button-span play-button-span`}>{Countrycuptrophy}<p className='play-icon-text'>Country Cup</p></span></button>
                </div>
               <label className='switch'>
                <input type='checkbox' id='soundeffect' checked={playerData.soundEffect} onChange={toggleSoundEffects}></input>
                <span></span>
                Sound Effects
               </label>
            </div>
            {showScreen==="leaderboard"&&<Leaderboard onclick={setShowScreenFalseFun}></Leaderboard>}
            {showScreen==="profile"&&<Profile onclick={setShowScreenFalseFun}></Profile>}
            {showScreen==='country_cup'&&<Countrycup disableLead={setShowScreenFalseFun}></Countrycup> }
            {showScreen==='survey'&&<Survey exit={setShowScreenFalseFun}></Survey>}
         
            
            </React.Fragment>
        )
    }
    else if(playerData.screen==='friend-room'){
        return(
            <FriendRoom socket={props.socket}></FriendRoom>

        )
    }
    else if(playerData.screen==='lobby'){
        return(<>

            <Lobby roomData={roomData} socket={props.socket}></Lobby>
            </>
        )
    }
    else if(playerData.screen==='match'){
        return(<React.Fragment>
            <Canvas roomData={roomData}></Canvas>
            <Main socket={ props.socket} roomData={roomData} ></Main></React.Fragment>)
    }




   
   

    // else if(true ){
       
        
    //     return(<React.Fragment><Canvas></Canvas><Main  ></Main></React.Fragment>)
    // }
   
    
}
export default Play;