import { back, play } from "./Bat";
import Modal from "./Modal";
import { profileImageSources } from "./Play";
import './profile.scss';

function Profile(props){

    const prof=localStorage;
    
    
    return(
        <Modal className="z-index" padding={true} onclick={props.onclick}>
             <button className="back-button center" onClick={()=>{play('back',0.9);props.onclick()}}>{back}</button>
            <div className="profile">

                <table>
                    <tbody>
                <tr className="table-img">
                        <td rowSpan={5}><img src={`${profileImageSources[prof.img]}`}></img></td>
                     
                    </tr>
                    <tr>

                        <td>Matches / Score</td>
                        <td>{prof.match!=null?prof.match:0} / {prof.score!=null?prof.score:0}</td>
                    </tr>
                    <tr>
                        <td>Avg / 30's</td>
                        <td>{prof.avg!=null?(Math.round(prof.avg*100)/100):0} / {prof.thirties!=null?prof.thirties:0}</td>
                    </tr>
                    <tr>
                        <td>50's</td>
                        <td>{prof.fifties!=null?prof.fifties:0}</td>
                    </tr>
                    <tr>
                    
                        <td>100's</td>
                        <td>{prof.hundreds!=null?prof.hundreds:0}</td>
                    </tr>
                    <tr>
                    <td rowSpan={1} className="table-name">{prof.name}</td>
                        <td>Wickets taken</td>
                        <td>{prof.wickets!=null?prof.wickets:0}</td>
                    </tr>
                    </tbody>
                </table>
                <div className="note-container">
                    <p className="note-1">Note</p>
                    <p className="note-2">Use the same browser and don't clear browser cache/cookies inorder to keep your progress</p>
                </div>
            </div>
            
        </Modal>
    )
}

export default Profile;