import React, { createContext, useContext, useState } from "react"



const PlayerData=createContext();
const UpdatePlayerData=createContext();

export function usePlayerData(){
    return useContext(PlayerData);
}

export function useUpdatePlayerData(){
    return useContext(UpdatePlayerData);
}

export function ContextProvider({children}){

    const [playerData,setPlayerData]=useState({name:'',profileImageName:'',opponentName:'',soundEffect:false,screen:'home'});
    function updatePlayerData(obj){
        setPlayerData((prev)=>{
            return {...prev,...obj}
        })
    }

    return(

        <PlayerData.Provider value={playerData}>
            <UpdatePlayerData.Provider value={updatePlayerData}>
                {children}
            </UpdatePlayerData.Provider>
         
        </PlayerData.Provider>

    )

}