import { useEffect, useState } from "react";
import { back,pcap, ocap, mom, mot, play } from "./Bat";
import Modal from "./Modal";
import './Cap.scss';

function Cap(props){

    const [sdata,setSdata]=useState([]);
    const [load,setLoad]=useState(false);

    useEffect(()=>{
        if(props.show==='o'){
            fetchmonthdata("orange");
        }
        else if(props.show==='p'){
            fetchmonthdata("purple");
        }
        else if( props.show==='mot'){
            fetchmonthdata('mot');
        }
        else if(props.show.split('--')[0]==0 || props.show.split('--')[0]==1 ){
            fetchmonthdata('topContributors'+props.show.split('--')[0]);
        }
    },[])
    async function fetchmonthdata (e){
        setLoad(()=>{return true});
        const res=await fetch(`https://www.handcricket.in/tournament/${e}`,{cache: "no-cache"});
        const data=await res.json();
        setSdata((prev)=>{return [...prev,...data]});
        
        setLoad(()=>{return false});
        
    }
    return(
        <Modal zindex={"full"} onclick={props.click} className={"cap"} >
<div className={`tournament-container cap-container ${props.show.split('--')[1]!=undefined?'mom':props.show}`}>
<div className={`tournament-heading-container cap-heading-container `}>
                    <div className="tournament-heading-container-1">
                    <button className="back-button" onClick={()=>{
                        props.click(); play('back',0.9)}} >{back}</button>
                    <div>                    {(props.show==="o"|| props.show==="p") && <h2>{props.show==="o"?ocap:pcap}{props.show==="o"?"Orange Cap":"Purple Cap"}</h2>}
                    {(props.show.split('--')[0]==0 || props.show.split('--')[0]==1 )&&<h2>Man of the Match</h2>}
                    {(props.show=="mot" )&&<h2 name="mot">Man of the Tournament</h2>}
                    {(props.show.split('--')[0]==0 || props.show.split('--')[0]==1 )&&<span>{props.show.split('--')[1]}</span>}                        </div>


                    </div>
                    </div>

<div  className={`country-cup-data-heading cap-data-heading`}>
                    <p>Rank</p>
                    <p>Player</p>
                    {(props.show==="o"|| props.show==="p")&& <p>{props.show==="o"?"Runs":"Wickets"}</p>}
                    {(props.show.split('--')[0]==0 || props.show.split('--')[0]==1 || props.show=="mot" )&&<p>Points</p>}
                </div>


                {load&&<div className="loader cap-loader"></div>}
                <div className="Leaderboard-data-container cap-data-container ">{!load&&sdata.length>0&&props.show==="o"? sdata.map((el,index)=>{
                return(<div key={index} className={`country-cup-data cap-data ${index+1===1?"orange-data":null}`}>
                    
                    <p>{index+1===1?ocap:index+1}</p>
                    <p className="team-name">{el.name}</p>
                    <p>{el.runs}</p>
                </div>
                )
            }):props.show==="p"?   sdata.map((el,index)=>{
                return(<div key={index} className={`country-cup-data cap-data ${index+1===1?"purple-data":null}`}>
                    
                    <p>{index+1===1?pcap:index+1}</p>
                    <p className="team-name">{el.name}</p>
                    <p>{el.wickets}</p>
                </div>
                )
            }): sdata.map((el,index)=>{
                return(<div key={index} className={`country-cup-data cap-data ${index+1===1?"mom-data":null}`}>
                    
                    <p>{index+1===1 ? props.show=="mot"?mot:mom:index+1}</p>
                    <p className="team-name">{el.name}</p>
                    <p>{el.points.toFixed(1)}</p>
                </div>
                )
            })  }</div>



                </div>
                <div className="note-cover">

               
                <div className="note-container remove-margin-note-container">
                    <p className="note-1">Note</p>
                    <p className="note-2">Use <b>unique names</b> as the runs and wickets are saved on the basis of the names</p>
                </div>
                </div>
        </Modal>
    )
}

export default Cap;