import React, { useEffect, useRef } from "react";

import { head,tails } from "./Bat";

import './Coin.css';


function Coin(props) {
    const coinRef=useRef();

    useEffect(()=>{

        let deg={Heads:1320,Tails:1350};
        let rotateTime=-(8-Number(props.time));




        let rotateDeg=deg[props.tossResult]*6;
         coinRef.current.style.setProperty('--my-end-deg',rotateDeg+'deg')
         coinRef.current.style.animationDelay= rotateTime+'s';
    },[]);
   function callInitiateTossResultValidationFuncInLobby(){
    props.initiateTossResultValidationFunc();
   }
    return(
       <React.Fragment>
 
    <div className={`coin ${props.tossResult}`} ref={coinRef }  onAnimationEnd={callInitiateTossResultValidationFuncInLobby} >
        {head}
        {tails}
    </div>
    </React.Fragment>
    )
}

export default Coin;