
import io from 'socket.io-client';
import { maleArray, femaleArray } from './Names';




export var botsocket={};

function randomRungenerator(num){
  
    if(num>=1 && num<=3){
        return [1,2,3,1,2,3,1,2,3,1,2,3,1,2,3,3][Math.floor(Math.random()*15)];
    }
   else{
    return [4,5,6,4,5,6,4,5,6,4,5,6,4,5,6,6][Math.floor(Math.random()*15)];
   }

}
 export function bot(roomId){
    let randomValue=Math.random();
    if(botsocket.id!=undefined&&botsocket.id.length>0){
        botsocket.disconnect();
        botsocket.connect();
    }
    else{
        botsocket = io.connect('https://www.handcricket.in/');
       
    }

    let gender=randomValue<0.90?"male":"female";
    let name;
    if(gender=='male'){
       name=  maleArray()[Math.floor(randomValue*maleArray().length)];
    }
    else{
        name= femaleArray()[Math.floor(randomValue*femaleArray().length)];
    }


    botsocket.emit('join-room',name,gender,{roomType:'random',botRoomId:roomId},'Limitless Block');
    setTimeout(()=>{
        botsocket.emit('acknowledgement','toss')
    },1000)

  

}
export function botCall(){
    setTimeout(()=>{botsocket.emit('toss-call',Math.random()>0.49?"Heads":"Tails")},(Math.floor(Math.random()*5000) +2000) );
}
export function botChoose(){
    setTimeout(()=>{botsocket.emit("toss-choose",Math.random()>0.49?"Bat":"Bowl")},(Math.floor(Math.random()*6000) +1000) );
}
export function botToss(){
    setTimeout(()=>{botsocket.emit('toss');},Math.floor(Math.random()*6000));
}
export function triggerRun(roomData,playerInput,playerName,botName){
    let num,timeoutSeconds=0,randomValue;
    if(roomData.batting==playerName && ((roomData.players[playerName].runs<=25   &&roomData.turn==0) || ( (roomData.players[playerName].runs/roomData.players[botName].runs)<0.5&&roomData.turn==1))){
        num=randomRungenerator(Math.ceil(Math.random()*6) );
    }
    else if(roomData.batting==botName && ((roomData.turn==0 && roomData.players[botName].runs<15)||(roomData.turn==1&&roomData.players[botName].runs/roomData.players[playerName].runs)<0.5)){
        num=Math.random()>0.25?randomRungenerator(playerInput+3):randomRungenerator(Math.ceil(Math.random()*6)) ;

    }   
    else {
        num=Math.random()>0.30?randomRungenerator(playerInput):randomRungenerator(playerInput+3);
    }
    
    randomValue=Math.random();

    if((randomValue<0.7 && roomData.timer>6) || randomValue<0.25){
        timeoutSeconds=randomValue*(roomData.timer-1)
    }
    setTimeout(()=>{ botsocket.emit('emit-run-data',num)},timeoutSeconds*1000)
   
}

export function botAnimationDone(){
    botsocket.emit('animation-done');
}