import React, { useEffect, useRef, useState } from "react";
import Modal from "./Modal";
import { Countrycuptrophy, back,ocap, pcap, mom, mot, play} from "./Bat";
import './Countrycup.scss';
import './Main.css';
import Cap from "./Cap";
import Fixture from "./Fixture";

var clickOn;

const teamShortForm={"Chennai Warriors":"CW","Gujarat Gaints":'GG','Delhi Tigers':'DT','Hyderabad Hurricanes':'HH','Kolkata Knights':'KK','Mumbai Blasters':'MB','Bangalore Strikers':'BS',
'Punjab Panthers':'PP','Rajasthan Rulers':'RR','Lucknow Superstars':'LS','TBD':'TBD'
}

function Countrycup(props){
    const [countrydata,setcountrydata]=useState({months:[],tabledatas:[],orange:[],purple:[],mot:[],fixture:{},currentMonth:'',currentDate:new Date(),playerstats:[],overallstats:[],whichDateDataToShow:0});
    const [isLoad,setLoad]=useState(true);
    const [which,setwhich]=useState({main:"country",sub:"current",showCap:false,showFixture:false});
    const selected=useRef();
    async function onload(){
        setLoad(true);
        selected.current.checked=true;
        const res1=await fetch('https://www.handcricket.in/tournament/months',{cache: "no-cache"});
        const data1=await res1.json();
        // const sortedData=data1.data1.table.sort((a,b)=>{return(b.runs-a.runs)}).sort((a,b)=>{return(b.points-a.points)});
        setcountrydata((prev)=>{return({...prev,whichDateDataToShow:new Date(data1.currentDate).getDate()>=28?28:new Date(data1.currentDate).getDate(),months:data1.data,tabledatas:data1.data1.table,orange:data1.data1.orange,purple:data1.data1.purple,mot:data1.data1.mot,fixture:data1.fixture,currentMonth:data1.currentMonth,currentDate:new Date(data1.currentDate)})});
        setLoad(false);
    }
    useEffect(()=>{
    onload();
    },[])

    function funwhich(e){
        if(e.target.value!=which.sub){
            setwhich((prev)=>{
                return({main:e.target.name,sub:e.target.value});
            })
        }
    }
    async function fetchmonthdata (e){
        if(countrydata.tabledatas.length==0 &&e.target.value==='current' ||countrydata.overallstats.length==0 && e.target.value==='alltime' ||countrydata.playerstats.length==0 && e.target.value==='playerstats' ){
        setLoad(true);
        const res=await fetch(`https://www.handcricket.in/tournament/${e.target.value}`,{cache: "no-cache"});
        const data=await res.json();
        if(e.target.value!='playerstats'){
            data.table=data.table.sort((a,b)=>{return(b.cups-a.cups)});
            data.table=data.table.sort((a,b)=>{return(b.runs-a.runs)}).sort((a,b)=>{return(b.points-a.points)});
        }

        setcountrydata((prev)=>{
             return({...prev,tabledatas:e.target.value==='playerstats' || e.target.value==='alltime'?prev.tabledatas: data.table,playerstats:e.target.value==='playerstats'?data:prev.playerstats,overallstats:e.target.value==='alltime'?data.table:prev.overallstats,orange:e.target.value==='current'?data.orange:prev.orange,purple:e.target.value==='current'?data.purple:prev.purple,mot:e.target.value==='current'?data.mot:prev.mot})});
        setLoad(false);
            }
        
    }
    function enableCap(e){
        
        setwhich((prev)=>{if(e!=undefined){ clickOn=e;
            if(e.target!=undefined){
               
            }
            else{
                play('click',0.42);
            }
        }
       ;return{...prev,showCap:!prev.showCap}});
    }
    function openFixtures(){
        setwhich((prev)=>{return{...prev,showFixture:!prev.showFixture}});
    }
    return(
       
        <React.Fragment>
             {which.showCap&&<Cap click={enableCap} show={clickOn}></Cap>}
             {which.showFixture&&<Fixture click={openFixtures} fixtures={countrydata.fixture} currentMonth={countrydata.currentMonth} currentDate={countrydata.currentDate} whichDateDataToShow={countrydata.whichDateDataToShow} ></Fixture>}
      
            <Modal onclick={props.disableLead} className="z-index" padding={true}>
                <div className="tournament-container">


                    <div className="tournament-heading-container">
                    <div className="tournament-heading-container-1">
                    <button className="back-button" onClick={()=>{play('back',0.9);props.disableLead()}} >{back}</button>
                    <div>
                    <h2>Country Cup {Countrycuptrophy} </h2> 
                    </div>
                    
                    </div>
                    </div>





                    {which.main=="country"&&   <div className="country-cup-radio-button">
                        <div>
                        <input ref={selected} type="radio" id="country-cup-table-1" name="country" value="current" onClick={(e)=>{funwhich(e);fetchmonthdata(e)}}></input>
                    <label htmlFor="country-cup-table-1">Table Points <svg  className="reset-icon" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth="1.5" stroke="currentColor">
  <path strokeLinecap="round" strokeLinejoin="round" d="M3.375 19.5h17.25m-17.25 0a1.125 1.125 0 01-1.125-1.125M3.375 19.5h7.5c.621 0 1.125-.504 1.125-1.125m-9.75 0V5.625m0 12.75v-1.5c0-.621.504-1.125 1.125-1.125m18.375 2.625V5.625m0 12.75c0 .621-.504 1.125-1.125 1.125m1.125-1.125v-1.5c0-.621-.504-1.125-1.125-1.125m0 3.75h-7.5A1.125 1.125 0 0112 18.375m9.75-12.75c0-.621-.504-1.125-1.125-1.125H3.375c-.621 0-1.125.504-1.125 1.125m19.5 0v1.5c0 .621-.504 1.125-1.125 1.125M2.25 5.625v1.5c0 .621.504 1.125 1.125 1.125m0 0h17.25m-17.25 0h7.5c.621 0 1.125.504 1.125 1.125M3.375 8.25c-.621 0-1.125.504-1.125 1.125v1.5c0 .621.504 1.125 1.125 1.125m17.25-3.75h-7.5c-.621 0-1.125.504-1.125 1.125m8.625-1.125c.621 0 1.125.504 1.125 1.125v1.5c0 .621-.504 1.125-1.125 1.125m-17.25 0h7.5m-7.5 0c-.621 0-1.125.504-1.125 1.125v1.5c0 .621.504 1.125 1.125 1.125M12 10.875v-1.5m0 1.5c0 .621-.504 1.125-1.125 1.125M12 10.875c0 .621.504 1.125 1.125 1.125m-2.25 0c.621 0 1.125.504 1.125 1.125M13.125 12h7.5m-7.5 0c-.621 0-1.125.504-1.125 1.125M20.625 12c.621 0 1.125.504 1.125 1.125v1.5c0 .621-.504 1.125-1.125 1.125m-17.25 0h7.5M12 14.625v-1.5m0 1.5c0 .621-.504 1.125-1.125 1.125M12 14.625c0 .621.504 1.125 1.125 1.125m-2.25 0c.621 0 1.125.504 1.125 1.125m0 1.5v-1.5m0 0c0-.621.504-1.125 1.125-1.125m0 0h7.5" />
</svg></label>
                        </div>
            <div>
            <input type="radio" id="country-cup-table-2" name="country"  onClick={(e)=>{funwhich(e);fetchmonthdata(e)}} value={"alltime"}></input>
                    <label htmlFor="country-cup-table-2">Team Stats <svg  className="reset-icon" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth="1.5" stroke="currentColor">
  <path strokeLinecap="round" strokeLinejoin="round" d="M3 13.125C3 12.504 3.504 12 4.125 12h2.25c.621 0 1.125.504 1.125 1.125v6.75C7.5 20.496 6.996 21 6.375 21h-2.25A1.125 1.125 0 013 19.875v-6.75zM9.75 8.625c0-.621.504-1.125 1.125-1.125h2.25c.621 0 1.125.504 1.125 1.125v11.25c0 .621-.504 1.125-1.125 1.125h-2.25a1.125 1.125 0 01-1.125-1.125V8.625zM16.5 4.125c0-.621.504-1.125 1.125-1.125h2.25C20.496 3 21 3.504 21 4.125v15.75c0 .621-.504 1.125-1.125 1.125h-2.25a1.125 1.125 0 01-1.125-1.125V4.125z" />
</svg></label>
            </div>
            <div>
            <input type="radio" id="country-cup-table-3" name="country"  onClick={(e)=>{funwhich(e);fetchmonthdata(e)}} value={"playerstats"}></input>
                    <label htmlFor="country-cup-table-3">Player Stats <svg className="reset-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 256" stroke="currentColor"><circle cx="84" cy="108" r="52" fill="none"  strokeLinecap="round" strokeLinejoin="round" strokeWidth="16"/><path d="M10.23,200a88,88,0,0,1,147.54,0" fill="none"  strokeLinecap="round" strokeLinejoin="round" strokeWidth="16"/><path d="M172,160a87.93,87.93,0,0,1,73.77,40" fill="none"  strokeLinecap="round" strokeLinejoin="round" strokeWidth="16"/><path d="M152.69,59.7A52,52,0,1,1,172,160" fill="none"  strokeLinecap="round" strokeLinejoin="round" strokeWidth="16"/></svg></label>
            </div>
              
                    </div>}
                    <div className="tournament-middle-container-1">

                    {/* {which.main=="country"&&which.sub=="current" &&
                   <div className="selection-div">
                   <select id="contribute" onChange={fetchmonthdata}>
                    {countrydata.months.map((e,ind)=> {
                        if(e.month!="alltime"){
                        const month=["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
                        return(<option className="contribute-option" key={ind} value={e.month==`${month[new Date().getMonth()]} - ${new Date().getFullYear()}`?"current" : e.month}>{e.month==`${month[new Date().getMonth()]} - ${new Date().getFullYear()}`?"Current" : e.month}</option>)
}})}
                   </select>
                   </div> } */}

                    {selected.current!=undefined&&selected.current.checked&&
                    <div className="play-off-info" onClick={openFixtures}>
                        <p>Today - {countrydata.whichDateDataToShow>=28?"Finals" :countrydata.whichDateDataToShow==25?'Qualifier 1':countrydata.whichDateDataToShow==26?'Eliminator':countrydata.whichDateDataToShow==27?'Qualifier 2':'League Match' } </p>
                        <div className="team-versus-cover">
                        {countrydata.fixture!=null && countrydata.fixture[countrydata.whichDateDataToShow]!=undefined&&countrydata.fixture[countrydata.whichDateDataToShow].map((e,ind)=>{
                            if(e!=null){
                                return(
                                    <div className="team-versus-container" key={ind}>
                                    <div className="single-team-versus">
                                        <img loading="lazy" src={require(`../images/flags/${e.team1[0]}.png`)} className={e.team1[0].replaceAll(" ","")}></img>
                                        <div>
                                            <p>{teamShortForm[e.team1[0]]}</p>
                                            <p>{e.team1[1]}</p>
                                        </div>
                                    </div>
                                    <p className="span">VS</p>
                                    <div className="single-team-versus">
                                        <img loading="lazy" src={require(`../images/flags/${e.team2[0]}.png`)} className={e.team2[0].replaceAll(" ","")}></img>
                                        <div>
                                            <p>{teamShortForm[e.team2[0]]}</p>
                                            <p>{e.team2[1]}</p>
                                        </div>
                                    </div>
                                    <p className="smaller-span">MoM : {e.mom}*</p>
                                </div>
                                )
                            }
                        })

                        }

                    </div>
                    </div>}
             
                   </div>


                   {selected.current!=undefined&&selected.current.checked&&   <div className="tournament-middle-container-2">
                   <button className="button-54" role="button" onClick={()=>{enableCap('o')}}  ><span ><span>{ocap}</span> <span  className="vsmaller-span">:</span> <span><span className="vsmaller-span">{countrydata.orange!=null?countrydata.orange[0]:'-'}</span> <span  className="vsmaller-span">{countrydata.orange!=null?'('+countrydata.orange[1]+')':''}</span></span></span></button>
                   <button className="button-54 mot" role="button" onClick={()=>{enableCap('mot')}}  ><span ><span>{mot}</span> <span  className="vsmaller-span">:</span>  <span><span  className="vsmaller-span">{countrydata.mot!=null?countrydata.mot[0]:'-'}</span> <span  className="vsmaller-span">{countrydata.mot!=null && countrydata.mot[1]!=undefined?'('+countrydata.mot[1].toFixed(1)+')':''}</span></span></span></button>
                   <button className="button-54 p" role="button" onClick={()=>{enableCap('p')}}  ><span ><span>{pcap}</span> <span  className="vsmaller-span">:</span> <span><span  className="vsmaller-span">{countrydata.purple!=null?countrydata.purple[0]:'-'}</span> <span  className="vsmaller-span">{countrydata.purple!=null?'('+countrydata.purple[1]+')':''}</span></span></span></button>

                   {/* <button class="button-54 mot" role="button" onClick={()=>{enableCap('mot')}} disabled={disableButton} >{countrydata.mot!=undefined?<span >{mot} : {countrydata.mot[0]} ({countrydata.mot[1]})</span>:<span >{mot} : -</span>}</button> */}
                    {/* <button class="button-54 p" role="button" onClick={()=>{enableCap('p')}} disabled={disableButton}>{countrydata.purple!=undefined?<span >{pcap} : {countrydata.purple[0]} ({countrydata.purple[1]})</span>:<span >{pcap} : -</span>}</button> */}
                    </div> }
                  
                   {which.main=="country"&&which.sub=="current"&&<div  className={`country-cup-data-heading`}>
                    <p>Pos</p>
                    <p>Team</p>
                    <p>P</p>
                    <p>W</p>
                    <p>L</p>
                    <p>D</p>
                    <p style={{'flexDirection': "column"}}>Pts<span>(runs)</span></p>
            
                </div>}
                
                            <div className="Leaderboard-data-container">{isLoad===true&&<div className="loader"></div>}{which.main=="country"&&which.sub=="current"&&countrydata.tabledatas.length!=undefined&&countrydata.tabledatas.map((el,index)=>{
                return(<div key={index} className={`country-cup-data`}>
                    
                    <p className={`${index>3?'eliminated':'qualified'}`}>{index+1}</p>
                    <p className="team-name"><img loading="lazy" src={require(`../images/flags/${el.team}.png`)} className={el.team.replaceAll(" ","")}></img>{el.team}</p>
                    <p>{el.played}</p>
                    <p>{el.won}</p>
                    <p>{el.lost}</p>
                    <p>{el.draw}</p>
                    {/* need to change after migration */}
                    <p style={{'flexDirection': "column"}}>{el.score !=undefined?el.score:el.points}<span>({el.runs})</span></p>
                    {/* need to change after migration */}
                </div>
                )
            })}</div>

                            <div className="Leaderboard-data-container">{isLoad===true&&<div className="loader"></div>}{which.main=="country"&&which.sub=="alltime"&&countrydata.overallstats.length!=undefined&&countrydata.overallstats.map((el,index)=>{
                return(<div key={index} className="country-cup-data overall-stats-data">
                    <p className="team-name"><img src={require(`../images/flags/${el.team}.png`)} className={el.team.replaceAll(" ","")} ></img>{el.team}</p>
                    <p>{Countrycuptrophy}&nbsp;x&nbsp;&nbsp;{el.cups}</p>
                </div>)
            })}</div>
                                       <div className="Leaderboard-data-container">{isLoad===true&&<div className="loader"></div>}{which.main=="country"&&which.sub=="playerstats"&&countrydata.playerstats.length>0&&countrydata.playerstats.map((el,index)=>{
                return(<div key={index} className="country-cup-data player-stats-data">
                    <p className="team-name">{el.name}</p>
                    <p style={{color:'rgb(255, 213, 0)'}}>{mot}&nbsp;x&nbsp;{el.mot==undefined?0:el.mot}</p>
                   <p style={{color:'rgb(247, 127, 0)'}}>{ocap}&nbsp;x&nbsp;{el.ocap==undefined?0:el.ocap}</p>
                  <p style={{color:'rgb(160, 108, 213)'}}>{pcap}&nbsp;x&nbsp;{el.pcap==undefined?0:el.pcap}</p>
                    <p style={{color:'rgb(140, 179, 105)'}}>{mom}&nbsp;x&nbsp;{el.mom==undefined?0:el.mom}</p>
                </div>)
            })}</div>

                            
         
                   
                 
                            </div>
                <div className="note-container remove-margin-note-container">
                    <p className="note-1">Note</p>
                    <p className="note-2">Play Random matches and contribute points to your favourite team. Top 4 teams enters the Play offs. <br></br>P <span className="arrow" >&#8674;</span> Play offs , E <span className="arrow">&#8674;</span> Eliminated</p>
                </div>
           
            
        </Modal>
        </React.Fragment>
    )
}

export default Countrycup;