
import { OrbitControls } from 'three/addons/controls/OrbitControls.js';
import { useEffect, useRef } from 'react';
import * as T from 'three';
import './Canvas.css';
import { play } from './Bat';
import { loader } from './Play';
import {  runClass, runDis } from './Main';
import { RoomEnvironment } from 'three/examples/jsm/environments/RoomEnvironment.js';

let lastid,firstTime=false;
let animationMixer;
let clips,idle;




const sizes={width:window.innerWidth,height:window.innerHeight};
const scene=new T.Scene();
const light =new T.AmbientLight(new T.Color("rgb(206, 230, 163)"),1.8);
scene.add(light);


const background={color:new T.Color("rgb(206, 230, 163)")};
scene.background=new T.Color(background.color);

const camera=new T.PerspectiveCamera(40,sizes.width/sizes.height);


camera.position.x=-2.8;
camera.position.y= 5.9;
camera.position.z= -106;

// camera.lookAt(new T.Vector3( 100, 1, 20 ));

scene.add(camera);
  
function Canvas() {
 
let boundingBox=loader.boundingBox
let boundingSphere=loader.boundingSphere
let boundingStump=loader.boundingStump
  const canvasRef=useRef();

  
  
  /** */
  const detectScreenChange=()=>{
    sizes.width=window.innerWidth;
    sizes.height=window.innerHeight;
    if(window.innerWidth>=470 &&window.innerWidth<1000){
      camera.position.x=-3.5;
      camera.position.y= 6;
      camera.position.z= -110;

      camera.fov=65;
       camera.zoom=(sizes.height/sizes.width)<1.5?(sizes.width/sizes.height)+0.10:(sizes.width/sizes.height)+0.06;
      camera.updateProjectionMatrix();
    }
    
          else if(window.innerWidth<=470){
            camera.fov=70;
            camera.position.z= -113;
            camera.position.x=-3;
            camera.zoom=(sizes.height/sizes.width)<1.5?(sizes.width/sizes.height)+0.43:(sizes.width/sizes.height)+0.28;
          
              camera.updateProjectionMatrix();
            }
            else{
              camera.zoom=1;
              camera.fov=40;
              camera.updateProjectionMatrix();
            }
  
    camera.aspect=sizes.width/sizes.height;
    camera.updateProjectionMatrix();
    renderer.setSize(sizes.width,sizes.height);
    
  }
  window.addEventListener('orientationchange',detectScreenChange)
    window.addEventListener('resize',detectScreenChange)

  /********* */




        



  let renderer,controls;



  useEffect(()=>{
    // idleFun();
    
    renderer=new T.WebGLRenderer({
      canvas:canvasRef.current,antialias:true
    });
    controls= new OrbitControls( camera, canvasRef.current);

    controls.update();
    renderer.setPixelRatio(Math.min(window.devicePixelRatio<2?1:window.devicePixelRatio,2));
    renderer.setSize(sizes.width,sizes.height);
    detectScreenChange();
    renderer.outputColorSpace=T.SRGBColorSpace;
    renderer.toneMapping=T.CineonToneMapping;
    const pmremGenerator = new T.PMREMGenerator( renderer );
    scene.environment = pmremGenerator.fromScene( new RoomEnvironment(), 0.04 ).texture;


    renderer.render(scene,camera);
    
    tick();
    return(()=>{
        // idle.stop();
        window.cancelAnimationFrame(lastid);
    })

  },[]);
  const clock=new T.Clock();
  const tick=()=>{
    animationMixer!=null&&animationMixer.update(clock.getDelta());
    
  
    if(boundingSphere!=undefined&&boundingBox!=undefined){
  
      // boundingSphere.center=scene.children[2].children[40].children[0].position;
      boundingSphere.center=scene.children[2].children[37].children[5].position;
      boundingBox.center=scene.children[2].children[37].children[6].position;
      boundingStump.center=scene.children[2].children[37].children[9].position;
      
      
    }

    if(boundingSphere!=undefined&&boundingStump!=undefined&&boundingSphere.intersectsSphere(boundingStump)&&firstTime){
      play(runDis.run,runDis.run==6?0.36:runDis.run==4?0.7:runDis.run==0?0.7:1);
        firstTime=false;
    }
    else if(boundingSphere!=undefined&&boundingBox!=undefined&&boundingSphere.intersectsSphere(boundingBox)&&firstTime){

      play(runDis.run,runDis.run==6?0.36:runDis.run==4?0.7:runDis.run==0?0.7:1);
    firstTime=false;
  }
    
    controls.update();
    renderer.render(scene,camera);
      lastid=window.requestAnimationFrame(tick);
    
  }

  return (
    <div className='canvas-container'>
      <canvas ref={canvasRef}></canvas>
    </div>
    
  );
}

export default Canvas;


export function exportDone(){
  scene.add(loader.glb.scene);
}
export function playFour(run){  
    
    
  animationMixer=loader.animationMixer;
  clips=loader.clips;  
  firstTime=true;
  const clip=T.AnimationClip.findByName(clips,runClass[run]);
  // if(run===5){
  //     playRun(5);
  // }
  const action=animationMixer.clipAction(clip);
  action.timeScale=run=='W'?1.2:1.4;
  action.stop();
  
  
  action.setLoop(T.LoopOnce);
  action.play();

}
export function idleFun(){
  animationMixer=loader.animationMixer;
  clips=loader.clips;
}