import { useEffect, useMemo, useRef, useState } from "react";
import Modal from "./Modal";
import "./Leaderboard.scss";
import { back, goat, leaderboardsvg, sevendaytrophy, thirddayleague,play } from "./Bat";

function Leaderboard(props){
    const [rankResult,setrankResult]=useState({rank:"",winner:"",where:"",clickedOn:"",weekend:0,monthend:0});
    const [isLoad,setLoad]=useState(true);
    let nextTourDateAndTime;
    let endsInTour = useRef(null)
    async function fetchData(data){
        setrankResult((prev)=>{
            return({...prev,clickedOn:data})
        });
        if(rankResult.where!=data){
        setLoad(true);
        const res=await fetch(`https://www.handcricket.in/leaderboard/${data}`,{cache: "no-cache"});
        const resjson=await res.json();
        resjson.rank=resjson.rank.slice(0,data==="week"?30:data==="month"?50:100);
        if(endsInTour.current==null){
            let today=new Date(resjson.currentDate);
            nextTourDateAndTime= new Date(today.getFullYear(), today.getMonth(),today.getDate()+1 );
            nextTourDateAndTime.setHours(0,0,0);
            let hrsLeft=Math.floor(((nextTourDateAndTime-today)/(1000*60*60))).toFixed(0)
            let minsLeft=(((nextTourDateAndTime-today)/(1000*60))%60).toFixed(0);
            endsInTour.current=(hrsLeft+(hrsLeft>1?'Hrs ':'Hr '))+(minsLeft+(minsLeft>1?'mins':'min')) ;
            setrankResult((prev)=>{
                return({...prev,weekend:7-(today.getDay()+1)!=0?7-(today.getDay()+1):endsInTour.current,monthend:new Date(today.getFullYear(),today.getMonth()+1,0).getDate()-today.getDate()!=0?new Date(today.getFullYear(),today.getMonth()+1,0).getDate()-today.getDate():endsInTour.current})});
        }

        setrankResult((prev)=>{
            return({...prev,...resjson,where:data})});
        setLoad(false);}
    }

    

    useEffect(()=>{
      
        fetchData("week");
    },[rankResult.length])
    return(
        <Modal className="z-index" padding={true} onclick={props.onclick}>
            <div className="Leaderboard">
            <div className="Leaderboard-heading-container">
                <div className="Leaderboard-main-heading remove-margin-container-auto">
            <button className="back-button" onClick={()=>{play('back',0.9);props.onclick()}} >{back}</button>
            <h2>Leaderboard {leaderboardsvg}</h2>
            </div>
            <div className="timeline-buttons">
                <button className={`timeline-buttons-button ${rankResult.clickedOn==="week"?'active':''}`} onClick={()=>{fetchData("week")}}><span><p >7 Day  </p><span className="championship">Championship</span></span>{sevendaytrophy} <span className="ends">Ends in {rankResult.weekend} {rankResult.weekend>0&&(rankResult.weekend>1?"Days":"Day")}</span></button>
                <button className={`timeline-buttons-button ${rankResult.clickedOn==="month"?'active':''}`} onClick={()=>{fetchData("month")}}><span><p >30 Day  </p><span className="championship">Championship</span></span>{thirddayleague}<span className="ends">Ends in {rankResult.monthend} {rankResult.monthend>0&&(rankResult.monthend>1?"Days":"Day")}</span></button>
                <button className={`timeline-buttons-button goat ${rankResult.clickedOn==="alltime"?'active':''}`} onClick={()=>{fetchData("alltime")}}><span><p >G.O.A.T</p></span>{goat}<span className="ends">All time</span> </button>
            </div>
            
            {isLoad===false&&rankResult.winner.name!=undefined&&rankResult.where!=="alltime"&&<div className="Leaderboard-winner">{rankResult.where==="week"?<span>7 Day Championship Winner : <span>{rankResult.winner.name}</span></span>:<span>30 Day Championship Winner : <span>{rankResult.winner.name}</span></span>}{rankResult.where==="alltime"?goat:rankResult.where==="month"?thirddayleague:sevendaytrophy} </div>}
            {isLoad===false&&rankResult.where!=="alltime"&& <div className="Leaderboard-headings">
                <p>Rank</p>
                <p>Name</p>
                <p>Wins</p>
                </div>}
                </div>
                {isLoad===true&&<div className="loader"></div>}
                {isLoad===false&&rankResult.rank.length>0&&rankResult.where!=="alltime"&&<div className="Leaderboard-data-container">
                {rankResult.rank.map((el,index)=>{
                return(<div key={index} className="Leaderboard-data">
                    <p>{index===0?(rankResult.where==="week"?sevendaytrophy:rankResult.where==="month"?thirddayleague:goat):index+1}</p>
                    <p>{el.name}</p>
                    <p>{el.wins}</p>
                </div>)
            })}
            </div>}
            {isLoad===false&&rankResult.rank.length>0&&rankResult.where==="alltime"&&<div className="Leaderboard-data-container">
                {rankResult.rank.map((el,index)=>{
                return(<div key={index} className="Leaderboard-goat-data">
                    <p>{el.name}{index===0?goat:null}</p>
                    <p>{thirddayleague} x {el.thirtyday}</p>
                    <p>{sevendaytrophy} x {el.sevenday}</p>
                </div>)
            })}
            </div>}
            </div>
           
        </Modal>    
    )
}

export default Leaderboard;